// Helper functions with improved error handling
export const handleApiError = (error) => {
  if (error.response?.data?.detail) {
    if (Array.isArray(error.response.data.detail)) {
      // Handle validation errors
      const messages = error.response.data.detail.map(err => 
        `${err.loc?.[1] || 'Field'}: ${err.msg}`
      );
      toast.error(messages.join('\n'));
    } else {
      toast.error(error.response.data.detail);
    }
  } else if (error.response?.data?.message) {
    toast.error(error.response.data.message);
  } else if (error.message) {
    toast.error(error.message);
  } else {
    toast.error('An unexpected error occurred');
  }
  
  console.error('API Error:', error);
};

// Token management
export const setAuthToken = (token) => {
  localStorage.setItem('access_token', token);
};

export const removeAuthToken = () => {
  localStorage.removeItem('access_token');
  localStorage.removeItem('user');
  localStorage.removeItem('refresh_token');
};

export const isAuthenticated = () => {
  const token = localStorage.getItem('access_token');
  if (!token) return false;
  
  try {
    // Check if token is expired (basic check)
    const payload = JSON.parse(atob(token.split('.')[1]));
    const now = Date.now() / 1000;
    return payload.exp > now;
  } catch (error) {
    console.error('Token validation error:', error);
    return false;
  }
};

export const getCurrentUser = () => {
  try {
    const user = localStorage.getItem('user');
    return user ? JSON.parse(user) : null;
  } catch (error) {
    console.error('Error parsing user data:', error);
    return null;
  }
};

export const setCurrentUser = (user) => {
  localStorage.setItem('user', JSON.stringify(user));
};

// File upload helpers
export const uploadFile = async (file, uploadUrl, onProgress) => {
  const formData = new FormData();
  formData.append('file', file);
  
  return api.post(uploadUrl, formData, {
    headers: { 'Content-Type': 'multipart/form-data' },
    onUploadProgress: (progressEvent) => {
      if (onProgress) {
        const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        onProgress(progress);
      }
    }
  });
};

// Cache management
export const clearApiCache = () => {
  // Clear any cached API responses if implemented
  localStorage.removeItem('api_cache');
};

// Network status
export const checkNetworkStatus = async () => {
  try {
    await api.get('/health');
    return true;
  } catch (error) {
    return false;
  }
};

// Export default API instance for custom requests
export default api;