import React, { useState, useEffect, createContext, useContext, Suspense, lazy } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import { ErrorBoundary } from 'react-error-boundary';
import { authAPI, setCurrentUser, getCurrentUser, isAuthenticated, handleApiError } from './services/api';

import LoadingSpinner from './components/LoadingSpinner';
import ErrorFallback from './components/ErrorFallback';
import ProtectedRoute from './components/ProtectedRoute';
import Layout from './components/Layout';
import NotFound from './components/NotFound';

const Auth = lazy(() => import('./components/Auth'));
const Dashboard = lazy(() => import('./components/Dashboard'));
const CourseView = lazy(() => import('./components/CourseView'));
const VideoPlayer = lazy(() => import('./components/VideoPlayer'));
const Quiz = lazy(() => import('./components/Quiz'));
const Analytics = lazy(() => import('./components/Analytics'));
const CourseDetail = lazy(() => import('./components/CourseDetail'));
const Profile = lazy(() => import('./components/Profile'));
const UserManagement = lazy(() => import('./components/admin/UserManagement'));



// Context for global state management
const AppContext = createContext();

export const useApp = () => {
  const context = useContext(AppContext);
  if (!context) {
    throw new Error('useApp must be used within an AppProvider');
  }
  return context;
};

const PageLoader = ({ message = "Loading..." }) => (
  <div className="min-h-screen flex items-center justify-center bg-gray-50">
    <div className="text-center">
      <LoadingSpinner size="large" />
      <p className="mt-4 text-gray-600 animate-pulse">{message}</p>
    </div>
  </div>
);

const useNetworkStatus = () => {
  const [isOnline, setIsOnline] = useState(navigator.onLine);

  useEffect(() => {
    const handleOnline = () => setIsOnline(true);
    const handleOffline = () => setIsOnline(false);

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  return isOnline;
};

const ScrollToTop = () => {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);
  return null;
};

function App() {
  const [user, setUser] = useState(null);
  const [initializing, setInitializing] = useState(true);
  const isOnline = useNetworkStatus();

  useEffect(() => {
    const initializeAuth = async () => {
      try {
        if (isAuthenticated()) {
          const userData = await authAPI.getCurrentUser();
          setUser(userData);
          setCurrentUser(userData);
        }
      } catch (error) {
        console.error('Failed to initialize auth:', error);
        localStorage.clear();
      } finally {
        setInitializing(false);
      }
    };
    initializeAuth();
  }, []);

  const handleLogin = (userData) => {
    setUser(userData);
    setCurrentUser(userData);
  };

  const handleLogout = async () => {
    try {
      await authAPI.logout();
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      setUser(null);
      localStorage.clear();
    }
  };

  const contextValue = {
    user,
    setUser,
    handleLogin,
    handleLogout,
    isOnline,
    isAuthenticated: isAuthenticated()
  };

  if (initializing) return <PageLoader message="Initializing application..." />;

  const OfflineNotification = () =>
    !isOnline && (
      <div className="fixed top-0 left-0 right-0 bg-red-600 text-white text-center py-2 z-50">
        <p className="text-sm">You are currently offline. Some features may not be available.</p>
      </div>
    );

  return (
    <ErrorBoundary
      FallbackComponent={ErrorFallback}
      onError={(error, errorInfo) => {
        console.error('App Error:', error, errorInfo);
      }}
    >
      <AppContext.Provider value={contextValue}>
        <Router>
          <div className="min-h-screen bg-gray-50">
            <ScrollToTop />
            <OfflineNotification />

            <Toaster
              position="top-right"
              toastOptions={{
                duration: 4000,
                style: {
                  background: '#363636',
                  color: '#fff',
                },
                success: {
                  style: {
                    background: '#10B981',
                  },
                },
                error: {
                  style: {
                    background: '#EF4444',
                  },
                },
              }}
            />

            <Suspense fallback={<PageLoader />}>
              <Routes>
                {/* Public Routes */}
                <Route
                  path="/login"
                  element={!isAuthenticated() ? <Auth mode="login" onLogin={handleLogin} /> : <Navigate to="/dashboard" replace />}
                />
                <Route
                  path="/signup"
                  element={!isAuthenticated() ? <Auth mode="signup" onLogin={handleLogin} /> : <Navigate to="/dashboard" replace />}
                />

                {/* Protected Routes */}
                <Route
                  path="/*"
                  element={
                    <ProtectedRoute>
                      <Layout user={user} onLogout={handleLogout}>
                        <Routes>
                          <Route path="/dashboard" element={<Dashboard user={user} />} />
                          <Route path="/courses" element={<CourseView user={user} />} />
                          <Route path="/course/:courseId" element={<CourseDetail user={user} />} />
                          <Route path="/course/:courseId/lesson/:lessonId" element={<VideoPlayer user={user} />} />
                          <Route path="/course/:courseId/quiz/:quizId" element={<Quiz user={user} />} />
                          <Route path="/analytics" element={<Analytics user={user} />} />
                          <Route path="/profile" element={<Profile user={user} onUserUpdate={setUser} />} />

                          {/* Admin-only routes */}
                          {user?.role === 'admin' && (
                            <>
                              <Route path="/admin/dashboard" element={<Analytics user={user} adminMode />} />
                              <Route path="/admin/courses" element={<CourseView user={user} adminMode />} />
                              <Route path="/admin/users" element={<UserManagement user={user} />} />
                            </>
                          )}

                          {/* Default redirect */}
                          <Route path="/" element={<Navigate to="/dashboard" replace />} />

                          {/* 404 Not Found */}
                          <Route path="*" element={<NotFound />} />
                        </Routes>
                      </Layout>
                    </ProtectedRoute>
                  }
                />
              </Routes>
            </Suspense>
          </div>
        </Router>
      </AppContext.Provider>
    </ErrorBoundary>
  );
}

export default App;
